.Switcher-Container {
  width: 120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  right: 20px;
  top: 20px;
}

.Switcher-Container .fa-sun,
.Switcher-Container .fa-moon {
  color: white;
  font-size: 20px;
}

.Switcher-Container .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.Switcher-Container .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.Switcher-Container .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #a7d7c5;
  transition: 0.4s;
}

.Switcher-Container .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

.Switcher-Container input:checked + .slider {
  background-color: #5f6769;
}

.Switcher-Container input:focus + .slider {
  box-shadow: 0 0 1px #5f6769;
}

.Switcher-Container input:checked + .slider:before {
  transform: translateX(26px);
}

.Switcher-Container .slider.round {
  border-radius: 34px;
}

.Switcher-Container .slider.round:before {
  border-radius: 50%;
}
